import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/subject';
import { DokobingoService } from './dokobingo.service';
import { SnsAccount } from './sns-account.enum';
import { User } from './user';


declare const hello;

/**
 * アカウントサービス
 */
@Injectable()
export class AccountService {
  private userChangeAnnouncedSource = new Subject<User>();
  userChangeAnnounced$ = this.userChangeAnnouncedSource.asObservable();

  user = new User();

  snsList: SnsAccount[] = [
    SnsAccount.facebook,
    SnsAccount.twitter,
    SnsAccount.google,
  ];

  constructor(private bingoService: DokobingoService) {
    // SNSアカウント認証初期化
    hello.init({
      facebook: '140868453294263',
      twitter: 'Pm5i79SC9PetmmiXrGeqZftfM',
      google: '997372776080-jkh3at1hp999nedgkijtg0td56a45je0.apps.googleusercontent.com'
    });

    this.getSnsLoginUserInfo().then(
      user => {this.announceUserChange(user as User)});
  }

  getUser() {
    return this.user;
  }

  updateLoginInfo() {
    return this.getSnsLoginUserInfo();
  }

  announceUserChange(user: User) {
    this.userChangeAnnouncedSource.next(user);
  }
  
  /**
   * SNSアカウントログイン
   * @param sns 
   */
  snsAccountLogin(snsKey: string) {
    var sns = SnsAccount[snsKey];
    console.log("SNS! " + sns);
    var self = this;
    return new Promise(
      function(resolve, reject) {
        hello(sns).login().then(
          () => { 
            self.logoutOther(sns);
            resolve();
          },
          (e) => { reject(e.error.message);
          }
        )
      }
    );
  }

  /**
   * SNSアカウントログアウト
   * @param sns 
   */
  snsAccountLogout(sns: string) {
    return new Promise(
      function(resolve, reject) {
        hello(sns).logout().then(
          ()  => { resolve();},
          (e) => { reject(e.error.message); }
        )
      }
    );
  }
  
  /**
   * SNSアカウントユーザー情報を取得する
   */
  getSnsLoginUserInfo() {
    var self = this;
    return new Promise(function(resolve, reject) {
      hello(self.snsList[0]).api('me').then(
        (json:any) => { resolve(self.createUserInfo(self.snsList[0], json)); }, 
        (e) => {
          hello(self.snsList[1]).api('me').then(
            (json:any) => { resolve(self.createUserInfo(self.snsList[1], json)); }, 
            (e) => {
              hello(self.snsList[2]).api('me').then(
                (json:any) => { resolve(self.createUserInfo(self.snsList[2], json)); }, 
                (e) => {
                  reject(e.error.message);
                });
            });
        });  
    });
  }

  /**
   * SNSアカウントユーザー情報指定以外をログアウト
   * 
   * @param keepLoginSns 
   */
  logoutOther(keepLoginSns:SnsAccount) {
    for (let sns in SnsAccount) {
      if (isNaN(Number(sns))) {
        if (sns != keepLoginSns) {
          this.snsAccountLogout(sns as SnsAccount)
            .then( () => console.log(`B! logout ${sns}`))
            .catch( e => console.log(`B! logout ${sns} error:${e}`))
            ;
        }
      }
    }
  }

  /**
   * ログインユーザー情報を正規化
   * 
   * @param sns 
   * @param json 
   */
  createUserInfo(sns:string, json:any):User {
    this.user.sns = sns;
    this.user.id = json.id;
    this.user.name = json.name;
    this.user.thumbnail = json.thumbnail;



    console.log(json);

    return this.user;
  }
}



